.mainContainer{
   
    height: 80vh;
    width:30vw;
    background-color: white;
    margin: 1rem auto;
    border-radius: 8px;
    padding: 10px;
}
h5{
  text-align: left;
 
}

td {
  border-bottom: 2px dashed grey;
}
.custom_field{
   padding-top: 1rem;
}
.weather_details{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  grid-gap: 5px;
}
.custom_field input {
    border: none;
    -webkit-appearance: none;
    -ms-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: #f2f2f2;
    padding: 8px;
    border-radius: 5px;
    width:20vw;
    font-size: 14px;
  }
  
  @media screen and (max-width: 400px) {
    .mainContainer {
        height: 70vh;
        /* border-bottom: 10px transparent ; */
        width:100vw;
        margin:0px auto 5px ;
        border-radius: 0px;
    }
    .custom_field input {
      border: none;
      -webkit-appearance: none;
      -ms-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: #c5c4c4;
      padding: 8px;
      border-radius: 5px;
      width:80vw;
      font-size: 14px;
    }
  }